export default {
	data(){
		return{
			imgList: [],
		};
	},
	props:{
		headPicShow:{ //图片
			type:[String,Boolean],
			default:true,
		},
		headPicValue:{
			type:String,
			default:require('@/static/imgs/st_pic.png')
		},
		
		headTitleShow:{ //标题
			type:[String,Boolean],
			default:true,
		},
		headTitleValue:{
			type:String,
			default:"满意度"
		},
		
		starsShow:{
			type:[String,Boolean],
			default:true,
		},
		starsMax:{ // 星星最大个数
			type:[String,Number],
			default:5,
		},
		starDefault:{ //未选中
			type:String,
			default:require('@/static/imgs/st_star.png'),
		},
		starActive:{
			type:String,
			default:require('@/static/imgs/st_star_active.png'),
		},
		score:{  //默认分数
			type:[Number,String],
			default:0
		},
		starsDisabled:{ //是否禁用star
			type:[Boolean],
			default:false
		},
		
		textareaShow:{ // 多行文本显示
			type:[String,Boolean],
			default:true,
		},
		textareaPlaceholder:{
			type:[String],
			default:"它满足你的期待吗？说说你的评价吧"
		},
		
		
		submitShow:{ // 发布按钮
			type:[String,Boolean],
			default:true,
		},
		submitText:{
			type:String,
			default:"发布",
		},
		
		infoReceive:{ // 获取值
			type:Object,
			default:function(){
				return {
					score:0,
					textareaValue:""
				}
			}
		},
		
	},
	computed:{
		formatScore(){
			return this.infoReceive.score
		}
	},
	methods: {
		/**
		 * @name 设置分数
		 */
		setScore(score){
			if(this.starsDisabled!==false)return
			this.infoReceive.score=score
			this.$emit("scoreChange",score)
		},
		
		/**
		 * @name 获取textarea内容
		 */
		
		blur(e){
			this.infoReceive.textareaValue=e.detail.value
		},
		
		/**
		 * @name 提交
		 */
		doSubmit(){
			this.$emit('submit',this.infoReceive)
		},
		ChooseImage() {
			uni.chooseImage({
				count: 4, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: (res) => {
					if (this.imgList.length != 0) {
						this.imgList = this.imgList.concat(res.tempFilePaths)
					} else {
						this.imgList = res.tempFilePaths
					}
				}
			});
		},
		ViewImage(e) {
			uni.previewImage({
				urls: this.imgList,
				current: e.currentTarget.dataset.url
			});
		},
		DelImg(e) {
			uni.showModal({
				title: '亲~',
				content: '确定要删除这张图片吗？',
				cancelText: '取消',
				confirmText: '确定',
				success: res => {
					if (res.confirm) {
						this.imgList.splice(e.currentTarget.dataset.index, 1)
					}
				}
			})
		},
	},
	created() {
		this.infoReceive.score=this.score
	}
}
